export default {
	data() {
		return {
			address : "",
			staus : "选择我的地址"
		}
	},
	onLoad() {
	    
	},
	onShow(){
	   if(uni.getStorageSync('addressno') == ""){
	   	this.staus = "选择我的地址";
	   }else{
	   	console.log(uni.getStorageSync('addressno'));
	   	this.staus = "修改我的地址";
	   }	
	},
	methods: {
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		
		openmap : function(){
			var that = this;
			uni.chooseLocation({
			    success: function (res) {
					var tmp = res;
					uni.showModal({
					        title: '是选择此位置',
					        content: tmp.name,
					        cancelText:'确定',
					        confirmText:'取消',
					        success(res){
					          if(res.cancel){
								  that.address = tmp.name;
								  uni.showToast({
								  	title: '添加成功',
									icon : "success"
								  });
								  
								 uni.setStorageSync('addressno',that.address);
					             console.log("点击了确定");
								 uni.navigateBack({
								 	
								 });
					          }else if(res.confirm){
					             console.log("点击了取消");
					          }
					        }
					      });
			    }
			});
		}
	}
}
